module.exports = {
  isNull: function isNull(input) {
    return input === null;
  },
  isNumeric: function isNumeric(input) {
    return !isNaN(input);
  },
  isNotNumeric: function isNotNumeric(input) {
    return isNaN(input);
  },
  isValidTime: function isValidTime(input) {
    var matchResult = input.match(/([01]?[0-9]|2[0-3]):[0-5][0-9]/);
    return matchResult !== null && Array.isArray(matchResult);
  },
  isNotValidTime: function isNotValidTime(input) {
    return !this.isValidTime(input);
  },
  splitBy: function splitBy(input, charForSplit) {
    return input.split(charForSplit);
  },
  floor: function floor(input) {
    return Math.floor(input);
  }
};