var utils = require('./utils.js');

module.exports = {
  checkKindOfTime: function checkKindOfTime(input) {
    if (utils.isNull(input)) this.throwError(6);
    var isAM = input.toLowerCase().indexOf('am') > -1;
    var isPM = input.toLowerCase().indexOf('pm') > -1;

    if (isAM || isPM) {
      return 'regular';
    } else {
      return 'military';
    }
  },
  convertMinsToTime: function convertMinsToTime(input) {
    var timeType = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 'military';
    if (utils.isNull(input)) this.throwError(7);
    var hour = utils.floor(input / 60);
    var mins = input % 60;

    if (mins < 10) {
      mins = '0' + mins;
    }

    var suffix = '';

    if (timeType == 'regular') {
      if (hour > 12) {
        hour -= 12;
        suffix = ' pm';
      } else {
        suffix = ' am';
      }
    }

    return hour + ':' + mins + suffix;
  },
  convertTimeToMins: function convertTimeToMins(input) {
    var timeType = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 'military';
    if (utils.isNotValidTime(input)) this.throwError(5);

    if (timeType == 'regular') {
      var input = this.switchRegularToMilitaryTime(input);
    }

    var timeElements = utils.splitBy(input, ':');
    var hour = timeElements[0];
    var mins = timeElements[1];
    return parseInt(hour) * 60 + parseInt(mins);
  },
  switchRegularToMilitaryTime: function switchRegularToMilitaryTime(input) {
    var isPM = input.indexOf('pm') > -1;
    var firstPartOfTime = utils.splitBy(input, ' ');
    var timeElements = utils.splitBy(firstPartOfTime[0], ':');
    var hour = timeElements[0];
    var mins = timeElements[1];

    if (isPM) {
      hour = parseInt(hour) + 12;
    }

    return hour + ':' + mins;
  },
  throwError: function throwError(msg) {
    throw new Error("Error (Code T" + msg + ")");
  },
  timePlusMins: function timePlusMins(startingTimeInMins, minsToAdd) {
    if (utils.isNull(startingTimeInMins)) this.throwError(1);
    if (utils.isNull(minsToAdd)) this.throwError(2);
    if (utils.isNotNumeric(startingTimeInMins)) this.throwError(3);
    if (utils.isNotNumeric(minsToAdd)) this.throwError(4);
    return startingTimeInMins + minsToAdd;
  }
};