var time = require('./src/time.js');

var events = require('./src/event.js');

var fashionablylate = module.exports = function (inputtedTime, inputtedEvent) {
  var timeType = time.checkKindOfTime(inputtedTime);
  var convertedTime = time.convertTimeToMins(inputtedTime, timeType);
  var eventsList = events.getEventList();
  var minsToAdd = events.getMinsToAdd(inputtedEvent, eventsList);
  var explanationMessage = events.getExplanationMessage(inputtedEvent, eventsList);
  var plussedTime = time.timePlusMins(convertedTime, minsToAdd);
  var plussedTimeBackForMore = time.convertMinsToTime(plussedTime, timeType);
  var data = {
    lateness: plussedTimeBackForMore,
    message: explanationMessage
  };
  return data;
};